 #root {
  background-color: #FFA657;
  font-family: 'Nunito', sans-serif;
  }
  
  /*This is the grey box containing logo, company name, and the user*/
  #logoBox{
      background-color: #595959;
      position: absolute;
      left: 0px;
      top: 0px;
      width: 100%;
      height: 9.1%;
      -webkit-box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.5);
              box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.5);
  }
  
  /*logo circle place holder-- have one circle element in HTML file as a place holder*/
  #logo{
      background-color: #363636;
      width: 6.1vh;
      height: 6.1vh;
      border-style: solid;
      border-width: 0.6vh;
      border-color: #FFFFFF;
      border-radius: 50%;
      margin-top: 0px;
      margin-bottom: 0px;
      margin-left:3.3%;
      display: inline-block;
      vertical-align: middle;
  }
  
  #logoInner{
      background-color: #363636;
      width: 4.6vh;
      height: 4.6vh;
      border-style: solid;
      border-width: 0.45vh;
      border-color: #31c4f3;
      border-radius: 50%;
      margin: auto;
  }
  
  #logoInnerInner{
      background-color: #363636;
      width: 3.1vh;
      height: 3.1vh;
      border-style: solid;
      border-width: 0.4vh;
      border-color: #31c4f3;
      border-radius: 50%;
      margin: auto;
  }
  
  /*o'hana!*/
  #logoName{
      color: #FFFFFF;
      font-family: 'Quicksand', sans-serif;
      font-size: 5.1vh;
      font-weight: 600;
      margin-top: 0px;
      margin-bottom: 0px;
      line-height: 1.8;
      display: inline-block;
      vertical-align: middle;
      margin-left: 1%;
  }
  
  #wrapper{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
      float: right;
      height: 100%;
      margin-right: 2%;
  }
  
  #greeting{
      color: #FFFFFF;
      font-family: 'Quicksand', sans-serif;
      font-size: 2.5vh;
      font-weight: 600;
      /*might consider using bootstrap*/
      margin: 0;
      line-height: 1.8;
      float: right;
  }
  


